
































import Vue from 'vue'
import { mapGetters } from 'vuex'
import Chart from '@/shared/components/Chart.vue'

export default Vue.extend({
  name: 'CovidChart',

  components: {
    Chart
  },

  data: () => ({
    defaultNotification: 'Data unavailable'
  }),

  computed: {
    ...mapGetters([
      'getSelectedGraphType',
      'getSelectedDataScale',
      'getCovidChartLabels',
      'getCovidChartData',
      'getSelectedCountry',
      'getSelectedState',
      'getSelectedCounty'
    ]),
    chartHeader() {
      return this.getSelectedDataScale.value === 'nationwide'
        ? this.getSelectedCountry
        : this.getSelectedDataScale.value === 'statewide'
          ? this.getSelectedState
          : this.getSelectedCounty
    }
  }
})
