





































import Vue from 'vue'

export default Vue.extend({
  name: 'Dropdown',

  props: {
    label: String,
    selectedItemLabel: String
  },

  data: () => ({
    displayDropdown: false
  }),

  methods: {
    closeOpenedDropdown() {
      if (this.displayDropdown) {
        this.displayDropdown = false
      }
    },
    toggleDropdown() {
      this.displayDropdown = !this.displayDropdown
    }
  }
})
